body {
  background-color: #f5f5f5;
  margin: 0;
  font-family: Lato, sans-serif;
  font-size: 16px;
}

.container {
  width: 80%;
  max-width: 600px;
  background-color: #fff;
  border: 1px solid #ff69b4;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  margin: 50px auto;
  padding: 20px;
  font-family: Lato, sans-serif;
  display: flex;
}

h1 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #ff69b4;
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-family: Lato, sans-serif;
  font-size: 3rem;
  font-weight: bold;
}

img {
  max-width: 100%;
  margin: 20px;
}

.stats {
  flex-direction: column;
  margin-bottom: 20px;
  font-size: 1.2rem;
  display: flex;
}

.stat {
  color: #ff69b4;
  justify-content: space-between;
  margin-bottom: 10px;
  display: flex;
}

.buttons {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  text-align: center;
  width: 45%;
  background-color: #ff69b4;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  text-decoration: none;
  transition: background-color .3s;
}

button:hover {
  background-color: #ffb6c1;
}

#message {
  text-align: center;
  color: #ff69b4;
  margin-top: 20px;
  font-family: Lato, sans-serif;
  font-size: 1.2rem;
}

#type-input, #name {
  color: #ff69b4;
}

#tamagotchi-form {
  color: #ff69b4;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

#tamagotchi-form label, #tamagotchi-form input, #tamagotchi-form select, #tamagotchi-form button {
  margin-bottom: 1rem;
}

